nav {
    position: absolute;
    top: 0;
    width: 100%;
    height: 50px;
    background: linear-gradient(45deg, rgb(156, 14, 1456), midnightblue);
}

.list {
    list-style-type: none;
    background: linear-gradient(45deg, rgb(156,14,156), midnightblue);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 22px;
}

.item {
    margin: 0 15px;
    text-transform: uppercase;
    color: #f1f1f1;
    cursor: pointer;
}

.btn {
    display: none;
    position: absolute;
    right: 10px;
    top: 7px;
    padding: 5px;
    color: rgb(35, 3, 68);
    font-size: 18px;
}

@media screen and (max-width: 500px) {
    .list {
        flex-direction: column;
        height: auto;
    }
    .item:nth-child(1){
        border-top: 1px solid #fff;
        margin-top: 50px;
    }
    .item {
        height: 70px;
        width: 100%;
        border-bottom: 1px solid #fff;
        line-height: 70px;
        text-align: center;
    }
    .btn {
        display: block;
    }
} 