.card {
    max-width: 300px;
    min-width: 300px;
    margin-top: 100px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    margin: 20px;
}
.card h2 {
    font-size: 30px;
    margin-bottom: 10px;
}
.card p {
    font-size: 18px;
}
.card a {
    text-align: left;
}

.article {
    max-width: 700px;
    min-width: 300px;
    margin: 100px auto;
}
.article h2 {
    font-size: 45px;
    margin-bottom: 20px;
}
.article p {
    font-size: 25px;
}